@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style:none;
  text-decoration: none;
  
}

:root {
  --color-bg: #212024;
  --color-bg-variant: #7C8C42;
  --color-primary: #BCBF50;
  --color-primary-variant: rgba(77, 255, 122, 0.4);
  --color-white:#fff;
  --color-light: rgba(255,255,255,0.6);

  --transition:all 400ms ease;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-ms: 90%
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  /* background-image: url(../src/assets/images/bg-texture.png); */
}

.container{
  width:var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  height: 100vh;
}

section > h2, section > h5 {
  text-align: center;
  color:var(--color-light);
}

section > h2 {
  color:var(--color-primary);
  margin-bottom: 3rem;
}

text-light{
  color: var(--color-light)
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color:var(--color-white);
  scale: 1.2;
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition)
}

.btn:hover{
  background-color: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}


.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}


@media screen and (max-width: 1024px) {
  .container{
    width: var(--container-width-md);
  }

  section{
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container{
    width: var(--container-width-ms);
  }

  section > h2{
    margin-top: 2rem;
  }
}